
import React from "react";
import { connect } from 'react-redux';
import { getCoursematerials, getCoursematerial, updateCoursematerial, deleteCoursematerial } from './../../actions/coursematerial';
import { getCoursescores } from './../../actions/coursescore';
import { getCoursecomments } from './../../actions/coursecomment';
import CourseFormAssessment from './../Form/CourseFormAssessment';
import CourseMaterialForm from './../Form/CourseFormMaterial';

// reactstrap components
import {
  Button,
  Row,
  Col,
  UncontrolledTooltip
} from "reactstrap";
import CardShow from "./CardShow";
import CardShowQuestionPerformance from "./CardShowQuestionPerformance";

const pics = {
  1 : 'fa-file-text',
  2 : 'fa-file-pdf',
  3 : 'fa-file-image',
  4 : 'fa-file-video',
  5 : 'fa-file-audio',
  6 : 'fa-youtube',
  7 : 'fa-link',
  8 : 'fa-comment',
  9 : 'fa-question',
  10 : 'fa-file-text'
}
class Material extends React.Component {
  constructor(props){
    super(props);
    this.state ={
      id:null,
      st:false,
      qid:null,
      qst:false,
      qdata:{},
      ast:null,
      poq:false
    }
  }


  showQuestion = (id, data) =>{
    //SET STATES TO OPEN MODULE
    this.setState({qid:id, qst:true, qdata:data});
    //GET ALL SCORES FOR STUDENTS THAT ATTEMTED THE TEST
    //GET QUESTION UNIQUE ID
    //MATERIAL_ID
    this.props.getCoursescores({'materialId': this.props.data.id , 'qid': id});
    //GET ALL COMMENTS FOR THE TEST
    this.props.getCoursecomments({'materialId': this.props.data.id , 'qid': id});
  }
  handleCloseQuestion = () =>{
    this.setState({qid:null, qst:false, qdata:{}});
  }

  handleActive = id =>{
    this.props.updateCoursematerial({'is_active':id}, this.props.data.id)
  }
  handleEdit = id =>{
    return <CourseMaterialForm mid={this.props.data.id}/>
  }
  handleDelete = id =>{
    this.props.deleteCoursematerial({'id':this.props.data.id})
  }
  showMaterial = () =>{
    this.setState({st:true})
  }
  

  render() {
    let multichoice_array = [1, 2, 3];
    let essay_array = [4, 5, 6, 7];
    let question = this.props.data.question;
    let que = JSON.parse(question);
    let am  = [];
    for(let prop in que)
    {
      let rw = que[prop] && que[prop] !== undefined ? que[prop] : {} ;
      if(multichoice_array.includes(parseInt(rw.type)))
      {
        let ar = [];
        ar.push(prop);
        ar.push(rw);
        am.push(ar);
      }
    }
    let tm = [];
    for(let prop in que)
    {
      let rw = que[prop] && que[prop] !== undefined ? que[prop] : {} ;
      if(essay_array.includes(parseInt(rw.type)))
      {
        let ar = [];
        ar.push(prop);
        ar.push(rw);
        tm.push(ar);
      }
    }

    let {qid, qst, qdata } = this.state;
    let butts =  tm.map((r, i)=>{
      return <Button key={i} type='button' size='sm' color='info' class="btn btn-info btn-sm" onClick={()=>this.showQuestion(r[0], r[1])}>Essay {i + 1}</Button>
      })

      
    return (
      <>
      
     {qst ? <CardShowQuestionPerformance
          mid={qid}
          st={qst}
          data={qdata}
          handleClose={()=>this.setState({qid:null, qst:false, qdata:{}})}
       />:''}
         <Row>
         {this.state.st ? 
         <CardShow 
          mid={this.props.data.id} 
          data ={this.props.data} 
          st={this.state.st}
          handleClose={()=>this.setState({st:false, id:null, data:{}})}
          />: ''}
            <Col md='12'>
              <a href="#" onClick={this.showMaterial} className="text-default">
                <h4>
                  <i class={`fa ${pics[this.props.data.types]}`}></i> <small style={{fontSize:14}}>{this.props.data.title}</small>
                </h4>
              </a>
            </Col>
            <Col md='12' className="text-left"> 
              <div class='btn-group btn-sm'>
                {this.state.ast ? <CourseFormAssessment 
                  mid={this.props.data.id} 
                  id={this.props.data.id} 
                  st={this.state.ast}
                  data={this.props.data}
                  handleClose={()=>this.setState({ast:false})}
                /> : ''}
                 <button class="btn btn-info" id={`too${this.props.data.id}`} onClick={()=>this.setState({ast:true})} handleClose={()=>this.setState({ast:false})} type="button"  >
                      <i class="fa fa-question"></i>
                </button>
                  <UncontrolledTooltip 
                      target={`too${this.props.data.id}`}
                  >
                    Questions
                    </UncontrolledTooltip>
                {parseInt(this.props.data.is_active) === 1 ? 
                <Button size='sm' class="btn-link btn-sm" onClick={()=>this.handleActive(0)}><i class="fa fa-eye"></i></Button>:
                <Button size='sm' class="btn-link btn-sm" onClick={()=>this.handleActive(1)}><i class="fa fa-eye-slash text-danger"></i></Button>}
                <Button size='sm' class="btn-link btn-sm" onClick={()=>this.props.handleEdit(this.props.data.id)}><i class="fa fa-edit"></i></Button>
                <Button size='sm' class="btn-link btn-sm" onClick={()=>this.props.handleDelete(this.props.data.id)}><i class="fa fa-trash"></i></Button>
              </div>
            </Col>
          </Row>
          <Row>
            {am.length > 0 ?
            <Button size='sm' class="btn btn-info btn-sm" onClick={()=>{this.showQuestion('mult', am)}}>Multichoice <span className="badge badge-secondary">{am.length}</span>
            <span className="sr-only"> questions</span></Button>
            : ''}
            {
             butts
            }
          </Row>
      </>
    );
  }
}


const mapStateToProps = (state, ownProps) => ({ 
  courses: state.courseReducer,
  user:state.userstudentReducer.user,
})

export default connect(mapStateToProps, { getCoursematerials, getCoursematerial, updateCoursematerial , deleteCoursematerial, getCoursescores, getCoursecomments})(Material)